<template>
    <div class="header">
        <div class="header-left">
            <div class="iconfont back-icon" >&#xe624;</div>
        </div>
        <div type="text" class="header-input">
            <sapn class="iconfont">&#xe632;</sapn>
            景点/游玩/主题
        </div>
        <router-link to="/city">
            <div class="header-right">
                {{this.city}}
                <span class="iconfont arrow-icon" >&#xe64a;</span>
            </div>
        </router-link>
    </div>

</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: "HomeHeader",
        data:function(){
            return{

            }
        },
        computed:{
            ...mapState(['city'])
        }
    }
</script>

<style  lang="stylus" scoped>
    @import'../../../assets/style/varibles.styl';
    .header{
        display:flex;
        height: 0.86rem;
        background: $bgColor;
        color:#fff;
    }
    .header .header-left{
        width: .64rem;
        float:left;
        line-height: 0.86rem;
    }
    .header .header-left .back-icon{
        text-align: center;
        font-size: 0.4rem;
    }
    .header .header-input{
        flex:1;
        height: 0.64rem;
        margin-top:0.12rem;
        margin-left: 0.2rem;
        padding-left: 0.2rem;
        background: #fff;
        line-height: 0.64rem;
        border-radius: 0.1rem;
        color:#ccc;
    }
    .header .header-right{
        min-width: 1.04rem;
        padding: 0.1rem;
        float:right;
        text-align:center;
        text-align: center;
        line-height: 0.86rem;
        color:#fff;
    }
    .header .header-right .arrow-icon{
        font-size: 0.24rem;
    }
</style>


