<template>
    <div class="icons">
        <swiper :options="swiperOption">
            <swiper-slide v-for="(page,index) of pages" :key="index">

                <div class="icon" v-for="item of page" :key="item.id">
                    <img class="icon-img" :src="item.imgUrl" alt="">
                    <p class="icon-desc">{{item.desc}}</p>
                </div>
            </swiper-slide>
        </swiper>
    </div>
</template>

<script>
    export default {
        name: "HomeIcons",
        props:{
            list:Array
        },
        data(){
          return {
              swiperOption:{
                  autoplay: false
              }
          }
        },
        computed:{
            pages (){
                const pages = []
                this.list.forEach((item,index)=>{
                    const page = Math.floor(index / 8)
                    if(!pages[page]){
                        pages[page] = []
                    }
                    pages[page].push(item)
                })
                return pages
            }
        }
    }
</script>

<style scoped >
    .icons >>>.swiper-container{
        height:0;
        padding-bottom:50%;
    }
    .icons{
        margin-top:0.1rem;
    }
    .icon{
        overflow: hidden;
        height: 0;
        float:left;
        width: 25%;
        padding-bottom:25%;
        text-align: center;
    }
    .icon-desc{
        margin-top:0.2rem;
    }
    .icon .icon-img{
        width: 60%;

    }
</style>


